export default class Vector {
    x: number
    y: number

    constructor(x: number, y: number) {
        this.x = x
        this.y = y
    }

    add(vector: Vector): this {
        this.x += vector.x
        this.y += vector.y

        return this
    }

    clone(): Vector {
        return new Vector(this.x, this.y)
    }


    static add(va: Vector, vb: Vector): Vector {
        return va.clone().add(vb)
    }

    static Zero(): Vector {
        return new Vector(0, 0)
    }
}