export default class CharCollisionDetection {
    private charset: string = '+=|æœ¨é—®å‡¶å›žå›šè‚Žçˆª~'

    constructor(custom_charset?: string) {
        if (custom_charset) this.charset = custom_charset
    }

    check(char_a: string, char_b: string): boolean {
        const a_solid = this.charset.indexOf(char_a) >= 0
        const b_solid = this.charset.indexOf(char_b) >= 0
        const has_solid = a_solid || b_solid

        // console.log('collide?', {
        //     has_solid, a_solid, b_solid, charset: this.charset, char_a, char_b
        // })

        return has_solid
    }

}