import Vector from "./renderer/Vector"
import Renderer from "./renderer/Renderer"
import Scene, { SceneData } from "./Scene"
import InputHandler from "./input/InputHandler"
import Tile from "./renderer/Tile"
import Color from "./renderer/Color"

const WIDTH = 20
const HEIGHT = 20

const SCREEN_SIZE = new Vector(WIDTH, HEIGHT)

const player_tile = new Tile({
    char: 'ðŸ˜€',
    color: Color.Red()
})

player_tile.getOrientationForDirection = (direction: string): number => {
    switch (direction) {
        case 'up': return 180
        case 'down': return 0
        case 'left': return 90
        case 'right': return 270
    }

    return 0
}

const sd_room = require('./simple_house.json') as SceneData
sd_room.player = player_tile

const renderer = new Renderer('game')
const scene_room = new Scene({
    data: sd_room,
    renderer,
    size: SCREEN_SIZE.clone()
})

const input_handler = new InputHandler()
scene_room.run(input_handler)

