import Layer from "./Layer"
import Tile from "./Tile"

interface RenderLayer {
    layer: Layer,
    z: number
}

const ratio = 1.13333

const makeDivLayer = (rLayer: RenderLayer, fontSize: number): HTMLElement => {
    const layer = rLayer.layer
    const layerEl = document.createElement('div')

    layerEl.classList.add('game-layer')
    layerEl.style.fontSize = `${fontSize}px`
    layerEl.style.top = `${ layer.pos.y * fontSize}px`
    layerEl.style.left = `${ layer.pos.x * fontSize * ratio}px`
    layerEl.style.height = `${ layer.size.y * fontSize}px`
    layerEl.style.width = `${ layer.size.x * fontSize * ratio}px`
    layerEl.style.zIndex = rLayer.z.toString()

    return layerEl
}

const makeDivOp = (id: string): HTMLElement => {
    const opEl = document.createElement('div')
    opEl.classList.add('game-tile')
    opEl.id = `game-${ id }`

    return opEl
}

const OpId = (id: string) => `game-${ id }`

export default class Renderer {

    private elemContainer: HTMLElement
    private layers: Record<string, RenderLayer> = {}
    private sortedLayers: RenderLayer[] = []

    private layerEls: Record<string, HTMLElement> = {}

    private fontSize: number = 30

    constructor(elementId: string) {
        this.elemContainer = document.getElementById(elementId)
    }

    addLayer(name: string, renderLayer: RenderLayer) {
        if (name in this.layers) return

        this.layers[name] = renderLayer
        this.sortedLayers.push(renderLayer)
        this.orderLayers()

        return this
    }

    orderLayers() {
        this.sortedLayers = this.sortedLayers.sort((a, b) => a.z - b.z)
    }

    clear() {
        this.layers = {}
        this.sortedLayers = []

        return this
    }

    commit() {
        for (let [name, rLayer] of Object.entries(this.layers)) {
            let layerEl = this.layerEls[name]

            if (!layerEl) {
                layerEl = makeDivLayer(rLayer, this.fontSize)
                this.elemContainer.appendChild(layerEl)
                this.layerEls[name] = layerEl
            }

            for (let op of rLayer.layer.drawOps) {
                let opEl = document.getElementById(OpId(op.tile.id))
                if (!opEl) {
                    opEl = makeDivOp(op.tile.id)
                    layerEl.appendChild(opEl)
                }

                if (op.isVisible) {
                    opEl.innerHTML = op.char.replace(/ /g, '&nbsp;')
                    opEl.style.color = op.color.cssColor()
                    opEl.style.backgroundColor = op.background.cssColor()
                    opEl.style.top = `${op.pos.y * this.fontSize}px`
                    opEl.style.left = `${op.pos.x * this.fontSize * ratio}px`
                    opEl.style.width = `${this.fontSize * ratio}px`
                    opEl.style.textAlign = 'center'
                    opEl.style.display = 'block'
                    if (op.orientation > 0) {
                        opEl.style.transform = `rotate(${ op.orientation }deg)`
                    } else {
                        opEl.style.transform = 'none'
                    }
                } else {
                    opEl.style.display = 'none'
                }
            }

            rLayer.layer.clear()
        }
    }

    static MakeLayer(z: number, layer: Layer): RenderLayer {
        return {
            z,
            layer
        }
    }

}