import Vector from "./Vector";
import Color from "./Color";
import Tile from "./Tile";

interface DrawOp {
    tile: Tile
    char: string
    color: Color
    background: Color
    pos: Vector
    isVisible: boolean,
    orientation: number,
}

const makeDrawOp = (tile: Tile): DrawOp => {
    return {
        tile,
        char: tile.char,
        color: tile.color.clone(),
        background: tile.background.clone(),
        pos: tile.pos.clone(),
        isVisible: tile.isVisible,
        orientation: tile.orientation
    }
}

interface LayerConstruct {
    opacity?: number
    pos?: Vector
    size: Vector
    isVisible?: boolean
}

export default class Layer {
    opacity: number
    pos: Vector
    size: Vector
    isVisible: boolean

    drawOps: DrawOp[] = []

    constructor(options: LayerConstruct) {
        this.opacity = options.opacity || 1
        this.pos = options.pos || Vector.Zero()
        this.size = options.size
        this.isVisible = options.isVisible || true
    }

    draw(tile: Tile) {
        this.drawOps.push(makeDrawOp(tile))
    }

    clear() {
        this.drawOps = []
    }
}