export default class Color {

    private _r: number
    private _g: number
    private _b: number
    private _a: number

    private _cssColor: string

    constructor(r: number, g: number, b: number, a: number) {
        this._r = r
        this._g = g
        this._b = b
        this._a = a

        this.makeCssColor()
    }

    get r(): number { return this._r }
    get g(): number { return this._g }
    get b(): number { return this._b }
    get a(): number { return this._a }

    set r(value: number) { this._r = value; this.makeCssColor() }
    set g(value: number) { this._g = value; this.makeCssColor() }
    set b(value: number) { this._b = value; this.makeCssColor() }
    set a(value: number) { this._a = value; this.makeCssColor() }

    makeCssColor() {
        this._cssColor = `rgba(${ this._r }, ${ this._g }, ${ this._b }, ${ this._a })`
    }

    cssColor(): string {
        return this._cssColor
    }

    clone(): Color {
        return new Color(this._r, this._g, this._b, this._a)
    }

    static Transparent(): Color {
        return new Color(0, 0, 0, 0)
    }

    static White(a: number = 1): Color {
        return new Color(255, 255, 255, a)
    }

    static Black(a: number = 1): Color {
        return new Color(0, 0, 0, a)
    }

    static Red(a: number = 1): Color {
        return new Color(255, 0, 0, a)
    }

    static Green(a: number = 1): Color {
        return new Color(0, 255, 0, a)
    }

    static Blue(a: number = 1): Color {
        return new Color(0, 0, 255, a)
    }

}