import Tile from "../renderer/Tile";
import { InputResponder } from "./InputHandler";
import Vector from "../renderer/Vector";

type OnPlayerNewPosition = (new_pos: Vector, player: Tile) => boolean

const ARROW = {
    UP: 'ArrowUp',
    DOWN: 'ArrowDown',
    LEFT: 'ArrowLeft',
    RIGHT: 'ArrowRight'
}

export default class PlayerHandler implements InputResponder {

    private player: Tile
    private last_move: number
    
    onPlayerNewPosition: OnPlayerNewPosition

    constructor(player: Tile, onPlayerNewPosition: OnPlayerNewPosition) {
        this.player = player
        this.onPlayerNewPosition = onPlayerNewPosition
        if (!this.onPlayerNewPosition) throw new Error('need player new position callback')
        if (!this.player) throw new Error('need player')
    }

    respond(action: string) {
        const now = Date.now()
        if (this.last_move) {
            const delta = now - this.last_move
            if (delta <= 100) return
        }

        let orientation = 'up'

        this.last_move = now
        let new_pos: Vector
        switch (action) {
            case ARROW.UP: {
                new_pos = Vector.add(this.player.pos, new Vector(0, -1))
                break;
            }
            case ARROW.DOWN: {
                new_pos = Vector.add(this.player.pos, new Vector(0, 1))
                orientation = 'down'
                break;
            }
            case ARROW.LEFT: {
                new_pos = Vector.add(this.player.pos, new Vector(-1, 0))
                orientation = 'left'
                break;
            }
            case ARROW.RIGHT: {
                new_pos = Vector.add(this.player.pos, new Vector(1, 0))
                orientation = 'right'
                break;
            }
        }

        if (!new_pos) return

        this.player.orient(orientation)
        const approved = this.onPlayerNewPosition(new_pos, this.player)
        if (!approved) return

        this.player.pos = new_pos
    }
    
}