import Color from "./Color"
import Vector from "./Vector"
import GenerateRandom from "./GenerateRandom"

interface TileConstruct {
    char?: string
    color?: Color
    background?: Color
    pos?: Vector
    isVisible?: boolean
    orientation?: number
}

export default class Tile {
    char: string
    color: Color
    background: Color
    pos: Vector
    isVisible: boolean
    orientation: number

    readonly id: string = GenerateRandom()

    getOrientationForDirection: (direction: string) => number

    private readonly orientation_map = {
        'up': 0,
        'down': 180,
        'left': 270,
        'right': 90,
    }

    constructor(options: TileConstruct) {
        this.char = options.char || ' '
        this.color = options.color || Color.White()
        this.background = options.background || Color.Black()
        this.pos = options.pos || Vector.Zero()
        this.isVisible = options.isVisible || true
        this.orientation = options.orientation || 0
    }

    orient(direction: string) {
        if (!this.getOrientationForDirection) {
            this.orientation = this.orientation_map[direction] || 0
        } else {
            this.orientation = this.getOrientationForDirection(direction)
        }
    }
}